.works{
    width: 100%;
    height: 100%;
    margin-top: 100px;

    .works-inner{
        width: 100%;
        height: 100%;

        display: flex;
        flex-direction: column;
        align-items: center;


        .works-inner-up, .works-inner-down{
            display: flex;
            flex-direction: row;
            align-items: center;
            height: fit-content;
            width: 100%;
            .work-item{
                height: 220px;
                width: 33.33%;
                transition-duration: 1s;
                transition-property: width;
                display: flex;
                align-items: center;
                justify-content: center;
                .work-item-image{
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                    background-position: center;
                }
                .work-item-description{
                    height: 100%;
                    width: 100%;
                    color: var(--white);
                    display: none;
                }
                &:hover{
                    background: var(--black);
                    width: 50%;                    
                    .work-item-image{
                        display: none;

                    }
                    .work-item-description{
                        display: inherit;
                        color: var(--white);
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        text-align: center;
                        width: 80%;
                        h3{
                            font-family: 'Poppins', Arial, Helvetica, sans-serif;
                            font-size: 24px;
                        }
                        p{
                            font-family: 'Roboto' ,Arial, Helvetica, sans-serif;
                            font-size: 18px;
                            font-weight: 300;
                            width: 100%;
                            padding: 0 5%;
                        }

                    }
                }                
            }
        }
        
    }
}