.logos{
    display: flex;
    flex-direction: column;
    width: 100%;
    .logos-inner{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        .logos-up, .logos-down{
            display: flex;
            flex-direction: row;
            .logos-item{
                width: fit-content;
                margin: 20px 80px;
                &:hover{
                    transform: scale(1.1);
                    transition: transform .8s cubic-bezier(0.25, 0.45, 0.45, 0.95);
                }
            }
        }
        .logos-down{
            .logos-item{
                .logos-item-img{                
                    width: 80px !important;
                }
            }
        }
    }
}