.front-end{
    width: 100%;
    height: fit-content;
    text-align: center;
    margin-bottom: 100px;
    .front-end-inner{
        width: 100%;
        height: 180px;
        display: flex;
        flex-direction: column;
        align-items: center;
        .front-end-title{
            width: 100%;
            height: 54.5%;
            h2{
                font-family: 'Poppins' ,Arial, Helvetica, sans-serif;
                font-weight: 300;
                font-size: 92px;
                color: var(--white);
                margin: 0 !important;
                letter-spacing: 8px;
            }
        }
        .web-developer{
            width: 45.5%;
            height: 46%;
            h2{
                font-family: 'Poppins' ,Arial, Helvetica, sans-serif;
                font-weight: 300;
                font-size: 25px;
                color: var(--white);
                margin: 0 !important;
                text-align: right;
                letter-spacing: 8px;
            }
        }
    }
}
