.description-container{
    height: 100%;
    width: 100%;
    margin-bottom: 40px;
    .description-inner{
        width: 100%;
        height: 420px;
        background: var(--blue);
        display: flex;
        flex-direction: column;
        padding: 3% 20%;
        h2{
            font-family: 'Poppins', Arial, Helvetica, sans-serif;
            font-size: 48px;
            font-weight: 500;
            color: var(--black);
            text-align: left;
            letter-spacing: 5px;
            line-height: 45px;
        }
        p{
            font-family: 'Roboto', Arial, Helvetica, sans-serif;
            font-size: 20px;
            font-weight: 300;
            color: var(--black);
            text-align: left;
        }
    }
}