.main{
    display: flex;
    height: 100%;
    width: 100%;
    padding-top: 40px;
    .left-column{
        height: 115%;
        width: 40px;
        background: white;

        position: absolute;
        left: 0;
        top: 0;
    }
    .right-column{
        height: 115%;
        width: 40px;
        background: white;

        position: absolute;
        right: 0;
        top: 0;
    }
    .main-inner{
        background: var(--black);
        width: 1200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 40px;
    }
}