.header{
    max-height: 40px;
    width: 100%;
    background: white;
    overflow: hidden;
    position: fixed;
    z-index: 999;
    .header-inner{
        display: flex;
        flex-direction: row;

        width: 100%;
        max-height: 40px;
    .header-text{
        width: 60%;
        text-align: left;
        padding-left: 38px;
        transform: translate(0, -7%);
        h2{
            font-family: 'Roboto', Arial, Helvetica, sans-serif;
            font-weight: 300;
            font-size: 24px;
            letter-spacing: 7px;
            color: var(--black);
        }
    }
    }
}